.advantage {
	&__heading {
		font-size: 18px;
		text-transform: uppercase;

		@include tablet {
			font-size: 25px;
		}

		@include desktop {
			font-size: 30px;
		}
	}
	&__text {
		@include tablet {
			padding-top: 30px;
		}
	}

	&__link-wrapper {
		display: flex;
		border-top: 1px solid rgba(0, 0, 0, 0.15);

		@include tablet {
			border-top: none;
		}
	}

	&__link {
		display: flex;
		align-items: center;
		margin-top: 20px;
		margin-bottom: 20px;

		@include tablet {
			margin-top: 45px;
			margin-bottom: 60px;
		}
	}

	&__link-text {
		margin-right: 15px;
		font-weight: 700;
		font-size: 14px;
		text-transform: uppercase;

		@include tablet {
			font-size: 16px;
		}

		@include desktop {
			font-size: 18px;
		}
	}

	&__arrow-right{
		@include tablet {
			margin-bottom: 4px;
		}
	}

	display: flex;
	flex-direction: column;
	justify-content: space-between;
	margin-bottom: 30px;
	padding: 20px $mobile-main-padding 0 $mobile-main-padding;
	background-color: $layout-highlight;

	@include tablet {
		padding: $tablet-main-padding $tablet-main-padding 0 $tablet-main-padding;
	}

	@include desktop {
		width: 50% - $desktop-inner-padding;
	}
}
