.header {
	&__nav {
		@include tablet {
			margin-left: auto;
		}
	}
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 4px 0;
	width: $mobile-main-width;
	// background-color: #fff;

	@include tablet {
		width: $tablet-main-width;
		padding-top: 20px;
	}

	@include desktop {
		width: $desktop-main-width;
		position: absolute;
		top: 0;
		padding-top: 50px;
	}
}
