.hero {
	&__heading {
		font-size: 30px;
		margin-block-end: 0em;
		margin-block-start: 0em;
		width: $mobile-main-width;
		color: #fff;

		@include tablet {
			font-size: 35px;
			line-height: 35px;
			color: darken($font-color, 5);
		}

		@include desktop {
			position: absolute;
			left: 13.19%;
			top: 200px;
			max-width: 35%;
			font-size: 40px;
			line-height: 50px;
		}
	}

	&__subheading {
		margin-top: -40px;
		width: $mobile-main-width;
		font-family: "Open Sans Condensed", sans-serif;
		font-weight: 300;
		font-size: 14px;
		text-transform: uppercase;

		@include tablet {
			margin-top: 35px;
			font-size: 16px;
		}

		@include desktop {
			position: absolute;
			left: 13.19%;
			max-width: 35%;
			top: 350px;
			font-size: 20px;
			font-weight: 600;
		}
	}

	&__statement {
		padding: 15px;
		width: 100vw;
		font-family: "Open Sans Condensed", sans-serif;
		font-weight: 600;
		font-size: 20px;
		text-align: center;
		text-transform: uppercase;
		background-color: $contrast-color;

		@include tablet {
			width: $mobile-main-width;
			margin-right: 0px;
			margin-top: 35px;
			margin-left: 0px;
			padding: 18px 29px;
		}

		@include desktop {
			max-width: 35%;
			position: absolute;
			left: 13.19%;
			top: 450px;
		}
	}

	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	margin-right: -$mobile-main-padding;
	margin-left: -$mobile-main-padding;
	padding-top: 20px;
	padding-bottom: 0;
	width: 100vw;
	height: 400px;
	background-image: url("/images/hero-600x400.jpg");
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	text-align: center;
	color: #fff;

	@include retina {
		background-image: url("/images/hero-1200x800.jpg");
	}

	@include tablet {
		width: 100%;
		height: auto;
		background-image: none;
		text-align: left;
		color: $font-color;
		padding: 60px $tablet-main-padding*3 90px $tablet-main-padding*3;
		margin-right: 0;
		margin-left: 0;
	}

	@include desktop {
		width: 50vw;
		height: 700px;
		background-image: url("/images/hero-d-960x700.jpg");
		background-position: top left;
		background-size: auto 100%;
		margin-left: auto;

		@include retina {
			background-image: url("/images/hero-d-1920x1400.jpg");
		}
	}
}
