.footer {
	&__icon {
		margin-top: -4px;
	}

	display: flex;
	align-items: center;
	justify-content: center;
	border-top: 1px solid rgba(0, 0, 0, 0.15);
	border-bottom: 1px solid rgba(0, 0, 0, 0.15);
	padding-top: 24px;
	padding-bottom: 24px;
	width: $mobile-main-width;

	@include tablet {
		border: none;
		justify-content: space-between;
		align-items: center;
		width: $tablet-main-width;
	}

	@include desktop {
		width: $desktop-main-width;
	}
}
