.main-nav__toggle-wrapper {
	&::before {
		content: "Меню";
		position: absolute;
		right: 75px;
	}

	display: flex;
	align-items: center;
	justify-content: center;
	width: 30px;
	height: 30px;

	@include tablet {
		display: none;
	}
}

.main-nav__items {

	@include tablet {
		display: flex;
	}

	padding-inline-start: 0;
	margin-block-start: 0;
    margin-block-end: 0;
	list-style: none;
}
.main-nav__item {
	&-link {
		font-weight: 600;
		font-size: 15px;
		text-decoration: none;
		text-transform: uppercase;
		color: $font-color;
		display: block;
		padding-top: 12px;
		padding-bottom: 12px;
		line-height: 20px;
		background: #fff;

		@include tablet {
			background: transparent;
		}

		@include desktop {
			color: #fff;
		}
	}

	&--active {
		border-bottom: 2px solid $accent-color;
		@include desktop {
			border-bottom: 2px solid #fff;
		}
	}

	margin-top: 20px;
	margin-right: 16px;
	margin-left: 16px;
	&:last-child{
		@include tablet {
			margin-right: 30px;
		}
	}

	@include tablet {
		margin-top: 0;
	}
}

.main-nav__toggle {
	&::before {
		content: "";
		position: absolute;
		left: 0;
		top: -10px;
		width: 30px;
		height: 2px;
		background-color: $font-color;
		transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
	}

	&::after {
		content: "";
		position: absolute;
		left: 0;
		top: 10px;
		width: 30px;
		height: 2px;
		background-color: $font-color;
		transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
	}

	&.main-nav__toggle--active {
		&::before {
			top: 0;
			transform: rotate(45deg);
			transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
		}

		&::after {
			top: 0;
			transform: rotate(-45deg);
			transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
		}

		background-color: transparent;
	}

	position: relative;
	width: 30px;
	height: 2px;
	background-color: $font-color;
}
.mobile_menu_overlay, .mobile_menu_container{
	position: fixed;
	top: 56px;
	left: 0;
	width: 100%;
	height: 100%;
}
.mobile_menu_container {
	@include tablet {
		top: 0;
	}
}

.mobile_menu_container, .mobile_menu_container ul li ul{
	-webkit-transition: all 200ms;
	-moz-transition: all 200ms;
	transition: all 200ms;
}
.mobile_menu_overlay{
	display: none;
	cursor: pointer;
	z-index: 10;
	background: rgba(0, 0, 0, 0.5);
}

.mobile_menu_container{
	transform: translateX(-300px);
	width: 270px;
	overflow: hidden;
	z-index: 11;
	background: #fff;

	@include tablet {
		transform: none;
		width: auto;
		height: auto;
		position: relative;
		background: transparent;
	}

}
.mobile_menu_container.loaded{
   	transform: translateX(0px);
}
.mobile_menu_container .mobile_menu_content{
	overflow: auto;
	max-height: 100%;
	padding-bottom: 30px;

	@include tablet {
		padding-bottom: 0;
		display: flex;
		align-items: center;
	}
}

@media (max-width: 320px){
	.mobile_menu_container{
		width: 240px;
	}
}
