.list-item {
	&__img {
		flex-shrink: 0;
		margin-right: 20px;
		width: 60px;
		height: 60px;
		background-color: $accent-color;
	}

	&__text {
		@include tablet {
			max-width: 65%;
			margin-top: 30px;
		}

		@include desktop {
			max-width: 100%;
		}
	}

	&__numeral {
		@include tablet {
			display: block;
			position: absolute;
			top: -46px;
			right: 5%;
			font-size: 370px;
			color: $layout-highlight;
			z-index: -1;
		}

		display: none;
	}

	display: flex;
	align-items: center;
	min-height: 122px;

	@include tablet {
		&:nth-child(even) {
			padding-left: 30px;
		}

		position: relative;
		flex-direction: column;
		align-items: start;
		max-width: 50%;
		height: 340px;
		padding-top: 30px;
		padding-bottom: 30px;
	}

	@include desktop {
		flex-wrap: no-wrap;
		max-width: (100% - $desktop-inner-padding * 6) / 4;
		flex-shrink: 1;
		&:not(:last-child) {
			margin-right: $desktop-inner-padding * 2;
		}
	}
}
