.course {
	&__heading {
		color: #fff;

		@include tablet {
			color: darken($font-color, 5);
		}

		@include desktop {
			font-size: 40px;
		}
	}

	display: flex;
	flex-direction: column;
	justify-content: center;
	margin-right: -$mobile-main-padding;
	margin-left: -$mobile-main-padding;
	padding: 10px $mobile-main-padding 20px $mobile-main-padding;
	width: 100vw;
	height: 300px;
	color: #fff;
	background-image: url("/images/course-pic.jpg");
	background-repeat: no-repeat;

	@include tablet {
		margin-right: -$tablet-main-padding*1.05;
		margin-left: -$tablet-main-padding*1.05;
		padding-right: $tablet-main-padding;
		padding-left: $tablet-main-padding;
		color: $font-color;
		border-top: 1px solid darken($layout-highlight, 5);
		background-image: none;
		padding-top: 60px;
		padding-bottom: 60px;
		height: auto;
	}

	@include desktop {
		margin-right: -$desktop-main-padding*1.2;
		margin-left: -$desktop-main-padding*1.2;
		padding-right: $desktop-main-padding;
		padding-left: $desktop-main-padding;
	}
}
