.button-link {
	&__whatsapp-icon {
		margin-right: 15px;
	}

	display: flex;
	align-items: center;
	justify-content: center;
	padding: 10px 20px;
	width: $mobile-main-width;
	font-weight: 300;
	font-size: 14px;
	text-align: center;
	text-decoration: none;
	text-transform: uppercase;
	color: #fff;
	background-color: $accent-color;

	@include tablet {
		width: fit-content;
		font-size: 20px;
	}
}
