// stylelint-disable no-empty-source
html {
	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		font-family: "Open Sans Condensed", sans-serif;
		color: darken($font-color, 5);
	}
	p {
		@include tablet {
			line-height: 29px;
		}
	}

	box-sizing: border-box;
	font-family: "Roboto", sans-serif;
	color: $font-color;
}
