.language-switch {
	&__link {
		text-decoration: none;
		color: $font-color;

		@include desktop {
			color: #fff;
		}
	}

	display: none;

	@include tablet {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: -7px;
		border: 2px solid $accent-color;
		border-radius: 50%;
		width: 28px;
		height: 28px;
	}

	@include desktop {
		border: 2px solid #fff;
		width: 50px;
		height: 50px;
	}
}

.footer .language-switch{
	border: 2px solid lighten($font-color, 50);
	&__link {
		color: lighten($font-color, 20);
	}

	@include desktop {
		width: 28px;
		height: 28px;
	}
}
